<template>
	<div 
		id="new_template"
	>
		<section class="template-create__header">
			<div class="wrapper">
				<h1>New WhatsApp template</h1>
				<div>
					<div>
						<i class="fa fa-whatsapp" />
						<span>Draft</span>
					</div>
					<div>
						<button>Save</button>
						<button>Submit</button>
					</div>
				</div>
			</div>
		</section>
		<div class="content">
			<v-container class="d-flex justify-space-around">
				<section class="form_section">
					<!-- <TemplateCreateFormVue /> -->
					<v-form
						id="form"
						ref="form"
						lazy-validation
					>
						<v-row>
							<v-col cols="8">
								<span>Sender(s)</span>
								<v-select
									v-model="selectedSenders"
									:rules="[v => !!v || 'Must select at least 1 item']"
									:items="sendersItems"
									:menu-props="{
										closeOnClick: true,
										closeOnContentClick: true,
									}"
									outlined
									filled
									dense
								/>
							</v-col>
							<v-col cols="4">
								<span>Template ID</span>
								<v-text-field
									outlined
									filled
									dense
								/>
							</v-col>
						</v-row>
						<span>Template name</span>
						<v-text-field
							v-model="templateName"
							:counter="60"
							:rules="[
								v => !!v || 'Required',
								v => v.length >= 3 || 'Must be at least 3 characters',
							]"
							required
							outlined
							filled
							dense
						/>
						<span>Select which template you want to create</span>
						<v-radio-group
							v-model="templateType"
							column
						>
							<v-radio
								label="Operator"
								value="operator"
							/>
							<p class="ml-8">
								This is a text template for an operator (service provider) that includes the required syntax and regular expressions. After you register such a template with the operator, you can create custom templates based on it which will allow you to send messages cheaper
							</p>
							<v-radio
								label="Custom"
								value="custom"
							/>
							<p class="ml-8">
								This is a text template based on an already registered operator template where you need to enter the required text and variables. Using this template will allow you to send your messages cheaper
							</p>
						</v-radio-group>
						<section v-if="templateType === 'operator'">
							<span>Template text</span>
							<ValidationProvider 
								v-slot="{ errors }"
								rules="required"
							>
								<mc-wysiwyg
									id="template-text-editor"
									v-model="templateText"
									height="150"
									@input="formatText"
								/>
								<span>{{ errors[0] }}</span>
							</ValidationProvider>
							<div class="template-text-editor__buttons mb-5">
								<EmojiPickerVue @select="emojiSelect" />
								<button
									class="add-parameter-button"
									title="Add parameter"
									@click.prevent="addVar"
								>
									<i class="fa-solid fa-code" />
								</button>
							</div>
							<span class="mt-5">Select a category</span>
							<v-select
								v-model="selectedCategory"
								:rules="[v => !!v || 'Must select at least 1 item']"
								:items="categoryItems"
								:menu-props="{
									closeOnClick: true,
									closeOnContentClick: true,
								}"
								required
								outlined
								filled
								dense
							/>
							Select a language
							<v-select
								v-model="selectedSenders"
								:items="sendersItems"
								:hide-details="true"
								:menu-props="{
									closeOnClick: true,
									closeOnContentClick: true,
								}"
								outlined
								filled
								dense
							/>
							<v-checkbox
								v-model="addHeader"
								:hide-details="true"
								:label="'Add a header'"
							/>
							<div 
								v-if="addHeader"
								class="ml-8 mt-5"
							>
								Header type
								<v-select
									v-model="selectedHeader"
									:rules="[v => !!v || 'Must select at least 1 item']"
									:items="headerTypes"
									:menu-props="{
										closeOnClick: true,
										closeOnContentClick: true,
									}"
									item-text="label"
									item-value="value"
									required
									outlined
									filled
									dense
								/>
								<div v-if="showHeaderTextSettings">
									Content
									<v-text-field
										v-model="selectedHeader.content"
										outlined
										filled
										dense
									/>
								</div>
							</div>
							<v-checkbox
								v-model="addMessageSignature"
								:hide-details="true"
								:label="'Add a message signature'"
							/>
							<div 
								v-if="addMessageSignature"
								class="ml-8 mt-5"
							>
								<v-text-field
									v-model="messageSignature"
									outlined
									filled
									dense
								/>
							</div>
							<v-checkbox
								v-model="addButtons"
								:label="'Add buttons'"
							/>
							<div v-if="addButtons">
								<v-radio-group
									v-model="buttonsType"
									column
								>
									<v-radio
										label="Chat buttons"
										:value="'chat'"
									/>
									<div v-if="buttonsType === 'chat'">
										<p class="ml-8">
											You can add up to 3 chat buttons. Each button can contain up to 25 characters.
										</p>
										<div 
											v-for="(button, index) in chatButtons"
											:key="index"
										>
											<div>
												<div>
													<h5>Button {{ index + 1 }}</h5>
													<button @click.prevent="deleteButton(index)">
														<i class="fa-regular fa-trash-can" />
													</button>
												</div>
												Button name
												<v-text-field
													v-model="button.name"
													outlined
													filled
													dense
												/>
												Button code
												<v-text-field
													v-model="button.code"
													outlined
													filled
													dense
												/>
											</div>
										</div>
										<button
											@click.prevent="addChatButton"
										>
											Add button +
										</button>
									</div>
									<v-radio
										class="mt-5"
										label="Call-to-action buttons"
										:value="'action'"
									/>
									<div v-if="buttonsType === 'action'">
										<p class="ml-8">
											You can add one call-to-action button and one сall button at the same time. A client can push these buttons several times.
										</p>
										<v-checkbox
											v-model="linkButtonSelect"
											:label="'Link'"
										/>
										<div
											v-if="linkButtonSelect" 
											class="linkButton ml-8"
										>
											Button name
											<v-text-field
												v-model="linkButton.name"
												outlined
												filled
												dense
											/>
											Button link. The maximum link length is 2000 characters
											<v-text-field
												v-model="linkButton.url"
												outlined
												filled
												dense
											/>
											<v-checkbox
												v-model="linkButton.dynamic"
												:label="'Make the button URL dynamic'"
											/>
										</div>
										<v-checkbox
											v-model="callButtonSelect"
											:label="'Call'"
										/>
										<div 
											v-if="callButtonSelect"
											class="ml-8"
										>
											Button name
											<v-text-field
												v-model="callButton.name"
												outlined
												filled
												dense
											/>
											Phone number (required a plus sign and the country code)
											<v-text-field
												v-model="callButton.phone"
												outlined
												filled
												dense
											/>
										</div>
									</div>
								</v-radio-group>
							</div>
						</section>
						Leave a comment if required
						<v-textarea
							v-model="templateComment"
							:no-resize="true"
							required
							outlined
							filled
							dense
						/>
					</v-form>
				</section>
				<section class="phone">
					<TemplateCreatePhoneButtonsVue @changeFrame="onChangeFrame" />
					<div class="phone_frame">
						<TemplateCreatePhoneFrameVue :frame="activeFrame" />
						<div class="phone_frame_container">
							<div class="phone_frame_container_position">
								<div class="phone_message">
									<div 
										v-if="selectedHeader.category === 'text' && selectedHeader.content"
										class="phone_message_header_text"
									>
										{{ selectedHeader.content }}
									</div>
									<div
										v-if="selectedHeader.category === 'attachment'" 
										class="phone_message_header_attachment"
									>
										<i 
											v-if="selectedHeader.type === 'video'"
											class="fa-regular fa-file-video"
										/>
										<i 
											v-if="selectedHeader.type === 'image'"
											class="fa-regular fa-image"
										/>
										<i 
											v-if="selectedHeader.type === 'file'"
											class="fa-solid fa-paperclip"
										/>
									</div>
									<div class="phone_message_content">
										<span
											v-if="formattedText"
											class="template_text"
											v-html="formattedText"
										/>
										<span
											v-if="messageSignature"
											class="template_signature"
										><br>{{ messageSignature }}</span>
										<span class="phone_message_time">
											<span>12:00</span>
										</span>
									</div>
								</div>
								<div 
									v-for="(button, index) in chatButtonsList"
									:key="index"
								>
									<div class="phone_message__button">
										{{ button.name }}
									</div>
								</div>
								<div 
									v-if="showLinkButton"
									class="phone_message__button"
								>
									<i class="fa-solid fa-arrow-up-right-from-square" /> {{ linkButton.name }}
								</div>
								<div 
									v-if="showCallButton"
									class="phone_message__button"
								>
									<i class="fa-solid fa-phone" /> {{ callButton.name }}
								</div>
							</div>
						</div>
					</div>
				</section>
			</v-container>
		</div>	
	</div>
</template>

<script>
import TemplateCreatePhoneFrameVue from '@/components/TemplateCreatePhoneFrame.vue';
import TemplateCreatePhoneButtonsVue from '@/components/TemplateCreatePhoneButtons.vue';
import EmojiPickerVue from '@/components/EmojiPicker.vue';
import { McWysiwyg } from '@mycure/vue-wysiwyg';

export default {
	name: 'WhatsappBusinessMailingNewTemplate',
	components: {
		TemplateCreatePhoneFrameVue,
		TemplateCreatePhoneButtonsVue,
		EmojiPickerVue,
		McWysiwyg
	},

	data() {
		return {
			content: '<h1>Some initial content</h1>',
			selectedSenders: [],
			sendersItems: [],
			activeFrame: 'android',
			templateName: '',
			templateType: 'operator',
			templateText: '',
			formattedText: '',
			templateComment: '',
			addHeader: false,
			selectedHeader: {
				category: 'text',
				content: ''
			},
			headerTypes: [
				{
					label: 'Text',
					value: {
						category: 'text',
						content: ''
					}
				},
				{
					label: 'Video',
					value: {
						category: 'attachment',
						type: 'video',
						content: ''

					}
				},
				{
					label: 'Image',
					value: {
						category: 'attachment',
						type: 'image',
						content: ''

					}
				},
				{
					label: 'File',
					value: {
						category: 'attachment',
						type: 'file',
						content: ''

					}
				}
			],
			addMessageSignature: false,
			messageSignature: '',
			addButtons: false,
			addChatButtons: true,
			addActionButtons: false,
			selectedCategory: '',
			categoryItems: ['Utility', 'Authentication', 'Marketing'],
			linkButtonName: '',
			linkButtonUrl: '',
			linkButtonDinamicUrl: false,
			variableCount: 0,
			actionButtons: [],
			chatButtons: [
				{
					name: '',
					code: ''
				}
			],
			buttonsType: 'chat',
			linkButtonSelect: false,
			linkButton: {
				name: '',	
				type: 'link',
				url: '',
				dynamic: false
			},
			callButtonSelect: false,
			callButton: {
				name: '',
				type: 'call',
				phone: ''
			},
		};
	},
	computed: {
		showHeaderTextSettings() {
			return this.addHeader && this.selectedHeader.category === 'text';
		},
		showHeaderAttachmentSettings() {
			return this.addHeader && this.selectedHeader.category === 'attachment';
		},
		chatButtonsList() {
			return this.chatButtons.filter(button => button.name);
		},
		showLinkButton() {
			return this.buttonsType === 'action' && this.linkButtonSelect && this.linkButton.name;
		},
		showCallButton() {
			return this.buttonsType === 'action' && this.callButtonSelect && this.callButton.name;
		}
	},
	watch: {
		templateType() {
			this.templateText = '';
			this.formattedText = '';
			// this.addHeader = false;
			// selectedHeader: {
			// 	category: 'text',
			// 	content: ''
			// },
		},
		buttonsType() {
			this.resetActionButtons();
		}
	},
	methods: {
		onChangeFrame(newFrame) {
			this.activeFrame = newFrame;
		},
		emojiSelect(emoji) {
			console.log(emoji);
			const textEditor = document.getElementById('editor');
			textEditor.append(emoji.data);
			this.templateText += emoji.data;
			this.formattedText += emoji.data;
		},
		resetActionButtons() {
			this.chatButtons = [
				{
					name: '',
					code: ''
				}
			];
		},
		formatText() {
			let text = this.templateText;
			let counter = 1;
			text = text.replace(/\*(.*?)\*/g, '<b>$1</b>');
			text = text.replace(/_(.*?)_/g, '<i>$1</i>');
			text = text.replace(/~(.*?)~/g, '<s>$1</s>');
			text = text.replace(/<span[^>]*>{{(.*?)}}<\/span>/g, () => {
				return `{{${counter++}}}`;
			});
			text = text.replace(/```(.*?)```/g, '<span style="font-family: monospace">$1</span>');
			this.formattedText = text;
		},
		addVar() {
			const textEditor = document.getElementById('editor');
			const parameter = document.createElement('span');
			parameter.innerHTML = '{{String of characters}}';
			parameter.style.cssText += `
				display: inline-block;
				white-space: nowrap;
				padding: 0px 4px;
				border-radius: 4px;
				background-color: rgb(84, 220, 160);
			`;
			parameter.setAttribute('contenteditable', 'false');
			textEditor.append(parameter);
			this.templateText += '<span contenteditable="false" style="white-space: nowrap; padding: 0px 4px; border-radius: 4px; background-color: rgb(84, 220, 160);">{{String of characters}}</span>';
			this.formatText();
			const selection = window.getSelection();
			const range = document.createRange();
			selection.removeAllRanges();
			range.selectNodeContents(textEditor);
			range.collapse(false);
			selection.addRange(range);
			textEditor.focus();
		},
		addChatButton() {
			this.chatButtons.push({
				name: '',
				code: ''
			});
		},
		deleteButton(index) {
			console.log(index);
			this.chatButtons.splice(index, 1);
		},
	},
};
</script>

<style lang="scss" scoped>
	#form::v-deep {
		color: black;
		.v-input__slot {
			margin: 0;
		}
		.v-input--selection-controls {
			margin-top: 0;
		}
		.v-input--radio-group--column .v-radio:not(:last-child):not(:only-child) {
			margin-bottom: 0px;
		}
	}
	.phone_message__button {
		font-size: 12px;
		line-height: 14px;
		width: 100%;
		text-align: center;
		word-break: break-word;
		margin-top: 2px;
		padding: 8px;
		color: rgb(26, 171, 252);
		background-color: rgb(247, 247, 247);
		border-radius: 8px;
	}
	// whatsappicon {
	// 	padding: 7px;
	// 	background-color: #25D266;
	// 	color: white;
	// 	border-radius: 50%;
	// 	font-size: 20px;
	// }

	.template-text-editor__buttons {
		display: flex;
		justify-content: space-between;
		width: 65px;
	}
	.add-parameter-button {
		width: 30px;
		height: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4px;
		border-radius: 8px;
		cursor: pointer;
		box-shadow: rgba(18, 18, 18, 0.2) 0px 1px 4px -1px;
	}
	.add-parameter-button:hover {
		background-color: rgba(0, 0, 0, 0.1);
	}
	.add-parameter-button:active {
		background-color: rgba(0, 0, 0, 0.2);
		box-shadow: rgba(18, 18, 18, 0.2) 0px 1px 4px 3px;
	}
	.add-parameter-button i {
		font-size: 12px;
	}
	#template-text-editor::v-deep {
		#toolbar {
			display: none;
		}
		#container {
			border: none;
		}
		#editor {
			border: 2px solid lightgray;
		}
		#editor:focus {
			border: 2px solid black;
		}
	}



	#new_template {
		color: #000000;
	}

	.wrapper {
		display: flex;
		flex: 1 1 auto;
		flex-flow: column;
		max-width: 100%;
		margin-bottom: 64px;
	}
	.phone {
		display: flex;
		flex-direction: column;
		min-width: 320px;
		position: sticky;
		top: 100px;
		left: 48px;
		width: 320px;
		height: max-content;
		align-items: flex-end;
		margin-left: 16px;
		&__buttons {
			max-width: max-content;
			margin-bottom: 16px;
			&__wrapper {
				display: flex;
				-webkit-box-pack: start;
				justify-content: flex-start;
				-webkit-box-align: center;
				align-items: center;
				position: relative;
				border-bottom: 5px solid rgb(236, 236, 236);
			}
			&__button_indicator {
				display: flex;
				-webkit-box-pack: center;
				justify-content: center;
				-webkit-box-align: center;
				align-items: center;
				position: relative;
				margin-bottom: -4px;
				padding: 16px;
			}
			// &__button {

			// }
		}
	}
	.empty_variable {
		display: inline-block;
		white-space: nowrap;
		padding: 0px 4px;
		border-radius: 4px;
		background-color: rgb(84, 220, 160);
	}
	.cursor {
		cursor: pointer;
	}
	.template_text {
		font-size: 12px;
		line-height: 18px;
	}
	.template_signature {
		font-size: 12px;
		line-height: 14px;
		color: rgb(183, 183, 183);
	}
	.phone_message_header_text {
		font-size: 12px;
		line-height: 14px;
		font-weight: 600;
		word-wrap: break-word;
		padding: 8px 0px 0px 8px;
	}

	.phone_message_header_attachment {
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		background-color: rgb(243, 249, 253);
		height: 160px;
		border: 8px solid rgb(255, 255, 255);
		border-radius: 12px;
		font-size: 25px;
	}

	.phone_frame_container_position::-webkit-scrollbar {
		width: 4px;
	}
	.content {
		background-color: white;
	}
	.form_section {
		width: 100%;
		max-width: 600px;
	}
	.phone_section {
		display: flex;
		flex-direction: column;
		min-width: 320px;
		position: sticky;
		top: 100px;
		left: 48px;
		width: 320px;
		height: max-content;
		align-items: flex-end;
		margin-left: 16px;
	}
	.phone {
		display: flex;
		flex-direction: column;
		min-width: 320px;
		position: sticky;
		top: 100px;
		left: 48px;
		width: 320px;
		height: max-content;
		align-items: flex-end;
		margin-left: 16px;
	}
	.types_buttons {
		max-width: max-content;
		margin-bottom: 16px;
	}
	.types_buttons_container {
		display: flex;
		-webkit-box-pack: start;
		justify-content: flex-start;
		-webkit-box-align: center;
		align-items: center;
		position: relative;
		border-bottom: 5px solid rgb(236, 236, 236);
	}
	.types_buttons_container__btn_indicator {
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		position: relative;
		margin-bottom: -4px;
		padding: 16px;
	}
	.phone_frame {
		position: relative;
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		flex-direction: column;
		width: 100%;
		min-height: 670px;
		border-radius: 4px;
	}
	.phone_frame_svg {
		width: 106%;
		height: 100%;
		position: absolute;
		top: 0px;
		left: 0px;
		border-radius: 39px;
		color: rgb(230, 220, 212);
	}
	.phone_frame_container {
		width: calc(100% - 23px);
		position: absolute;
		top: 0px;
		left: 0px;
		margin: 39px 0px 0px 11px;
		overflow: hidden;
		height: calc(100% - 66px);
		border-radius: 30px 33px 34px 32px;
	}
	.phone_frame_container_position {
		height: 100%;
		overflow: hidden auto;
		padding: 48px 32px 20px 16px;
	}
	.phone_message {
